import React from 'react';
import styles from './profileinfo.module.css';
import defaultAvatar from '../../../../assets/images/defaultAvatar.png'
import Preloader from '../../../common/Preloder/Preloader';
import Status from './Status/Status';

export default function ProfileInfo(props) {

  if (!props.profile) {
    return <Preloader />
  }

  return (
    <div className={styles.profileInfo}>
      <div className={styles.wallpaper}>
        <img src="https://gamerwall.pro/uploads/posts/2022-03/1648197161_2-gamerwall-pro-p-krasivii-fon-dlya-profilya-krasivie-2.jpg" alt="info" />
      </div>
      <div className={styles.descriptionBox}>
        <div className={styles.description}>
          <div className={styles.avatarBox}>
            <img src={props.profile.photos.large ? props.profile.photos.large : defaultAvatar} alt="аватар" />
          </div>
          <div className={styles.userInfo}>
            <h1 className={styles.fullName}>{props.profile.fullName}</h1>
            <div className={styles.addInfo}>
              <div>
                <p>{props.profile.lookingForAJob ? 'I need a job' : 'Do not find a job'}</p>
                <p>{props.profile.lookingForAJobDescription ? 'I need a good job' : 'No'}</p>
              </div>
              <div>
                <p>Contacts</p>
                <ul>
                  <li>1</li>
                  <li>2</li>
                  <li>3</li>
                  <li>4</li>
                  <li>5</li>
                  <li>6</li>
                  <li>7</li>
                  <li>8</li>
                </ul>
              </div>
            </div>
            <p>{props.profile.aboutMe ? props.profile.aboutMe : 'About me'}</p>
          </div>
        </div>
        <Status
          status={props.status}
          updateStatus={props.updateStatus}
        />
      </div>
    </div>
  );
}
