import React from 'react';
import Profile from './Profile';
import { getUser, getStatus, updateStatus } from '../../../redux/profileReducer';
import { useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { useParams } from 'react-router-dom';
import { withAuthNavigate } from '../../../HOC/withAuthNavigate';
import { selectProfile, selectStatus } from '../../../redux/profileSelectors';
import { selectAuthorizedUserId } from '../../../redux/authSelectors';

const ProfileComponent = withAuthNavigate(ProfileContainer);

function ProfileContainer(props) {

  const authirizedUserId = useSelector(selectAuthorizedUserId)
  const profile = useSelector(selectProfile);
  const status = useSelector(selectStatus);

  let dispatch = useDispatch();

  let { userId } = useParams();

  if (!userId) {
    userId = authirizedUserId;
  }

  useEffect(() => {
    dispatch(getUser(userId));
    dispatch(getStatus(userId));
  }, [dispatch, userId]);

  const onUpdateStatus = (status) => dispatch(updateStatus(status))

  return (
    <Profile
      profile={profile}
      status={status}
      updateStatus={onUpdateStatus}
    />
  );
}

export default ProfileComponent;
